import Vue from "vue";
import VueTestComponent from "../components/VueTestComponent.vue";
import vuetify from "vuetify";
function HelloWorld() {
    console.log("Hello World");
    /* DECLARE VUETIFY */
    var vtf = new vuetify(); //VUETIFY_CONFIG);
    Vue.use(vuetify);
    new Vue({
        el: '#app-main',
        data: {
            MotivationalPhrase: 'You so can do it!',
        },
        components: {
            VueTestComponent: VueTestComponent
        },
        /* ASSIGN VUETIFY */
        vuetify: vtf
    });
}
$(document).ready(function () {
    HelloWorld();
});
